<template>
  <div>
    <div class="ml-4 pt-2 header-text-style font-weight-bold">
      일주일간 낙상 건수
    </div>
    <v-divider class="mt-2" />
    <apexchart
      v-if="loading"
      type="bar"
      width="100%"
      :height="setChartHeight()"
      :options="chartOptions"
      :series="series"
    >
    </apexchart>
  </div>
</template>
<script>
export default {
  watch: {},
  async created() {},
  async mounted() {
    await this.getFallDetection();
  },
  methods: {
    async getFallDetection() {
      try {
        var result = await this.$http.post("events/fall_detect/all", {
          uid: this.$session.user.id,
        });
        this.series.push({ name: "Fall", data: result.data });
        this.loading = true;
      } catch {
        this.series.push({ name: "Fall", data: [] });
        this.loading = true;
      }
    },
    setChartHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 100;
        case "sm":
          return 120;
        case "md":
          return 120;
        case "lg":
          return "145vh";
        case "xl":
          return "260vh";
      }
    },
  },
  data() {
    return {
      chartOptions: {
        colors: ["#ff0000"],
        chart: {
          type: "bar",
        },
        dataLabels: {
          enabled: true,
          style: {
            fontSize: "16px",
            fontFamily: "Helvetica, Arial, sans-serif",
            fontWeight: "bold",
          },
        },
        xaxis: {
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          fill: {
            colors: ["#ff0000"],
          },
          labels: {
            style: {
              fontSize: "18px",
              fontFamily: "Helvetica, Arial, sans-serif",
              fontWeight: 400,
              cssClass: "apexcharts-xaxis-label",
            },
          },
          tooltip: {
            enabled: true,
          },
        },
        yaxis: {
          show: false,
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          tickAmount: 1,
          floating: false,
        },
      },

      series: [],
      loading: false,
    };
  },
};
</script>
<style lang="scss">
@media screen and (min-width: 3000px) {
  .header-text-style {
    font-size: 1.4rem;
  }
  .min-max-text-style {
    font-size: 1.4rem;
  }
  .time-top-margin {
    margin-top: 24px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 1.4rem;
  }
  .chart-height {
    height: 200vh;
  }
}
@media screen and (min-width: 1904px) {
  .header-text-style {
    font-size: 1.3rem;
  }
  .min-max-text-style {
    font-size: 1.2rem;
  }
  .time-top-margin {
    margin-top: 24px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 1.2rem;
  }
  .time-text-style {
    font-size: 3.6rem;
  }
  .chart-height {
    height: 200vh;
  }
}
@media screen and (max-width: 1903px) {
  .header-text-style {
    font-size: 1.2rem;
  }
  .min-max-text-style {
    font-size: 1rem;
  }
  .time-top-margin {
    margin-top: 24px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 1rem;
  }
  .time-text-style {
    font-size: 2.4rem;
  }
  .chart-height {
    height: 140px;
  }
}
</style>
