import Vue from "vue";
import io from "socket.io-client";
import { restapi, sockapi } from "./http";

export default new Vue({
  methods: {
    setSession(user, skipStore) {
      this.user = user;
      restapi.defaults.headers.common["token"] = user.token;
      sockapi.defaults.headers.common["token"] = user.token;

      if (!skipStore) localStorage.setItem("SESSION", JSON.stringify(user));

      let query = { token: user.token };
      this.socket = io("/receiver", {
        query: query,
        transports: ["websocket"],
      });
      // this.socket.on("message", this.onMessageHandler.bind(this));
      this.socket.on("efwbsync", this.onDataHandler.bind(this));
      this.socket.on("efwbasync", this.onEventHandler.bind(this));
      this.socket.on("gateway_connect", this.onBandNumHandler.bind(this));
      this.socket.on("error", this.onErrorHandler.bind(this));
      this.socket.on("connect_error", this.onErrorHandler.bind(this));
      this.socket.on("example", this.onExampleHandler.bind(this));
    },
    async loadSessionData() {
      try {
        let str = localStorage.getItem("SESSION");
        if (!str) {
          return false;
        } else {
          var res = await restapi.post(
            "access_history/reload",
            JSON.parse(str)
          );
          if (res.data === "") {
            this.releaseSession();
            return false;
          }
        }
        let json = JSON.parse(str);
        this.user = json;
        this.setSession(json, true);
        return true;
      } catch (error) {
        console.error(error);
        localStorage.removeItem("SESSION");
        return false;
      }
    },
    async historyCheck() {
      var session = localStorage.getItem("SESSION");
      if (session != null) {
        var res = await restapi.post(
          "access_history/reload",
          JSON.parse(session)
        );
        if (res.data === "") {
          this.releaseSession();
          return false;
        } else {
          return true;
        }
      } else return false;
    },
    releaseSession() {
      restapi.post("logout");
      restapi.defaults.headers.common["token"] = "";
      delete restapi.defaults.headers.common["token"];
      sockapi.defaults.headers.common["token"] = "";
      delete sockapi.defaults.headers.common["token"];
      localStorage.removeItem("SESSION");
      this.user = null;

      if (this.socket) this.socket.close();
      else console.error("No Socket Instance");
    },
    onErrorHandler(err) {
      switch (err) {
        case "unauthorized": {
          // TODO: 에러 핸들링
          this.releaseSession();
          break;
        }
        case "xhr poll error": {
          break;
        }

        // default: {

        // }
      }
    },
    onDataHandler(data) {
      this.$emit("data", data);
    },
    onExampleHandler(data) {
      console.log(data);
    },
    onEventHandler(data) {
      this.$emit("efwbasync", data);
    },
    onBandNumHandler(data) {
      this.$emit("gateway_connect", data);
    },
    onGatewayDisconnectHandler(data) {
      var gateway = {
        panid: data,
        bandnum: 0,
        connectstate: false,
      };
      this.$emit("gateway_connect", gateway);
    },
  },
  created() {
    this.loadSessionData();
  },
  data() {
    return {
      user: null,
      group: "",
      groupstr: "",
      selectBand: null,
      bandinfo: [],
      socket: null,
      data: null,
      load: false,
      showConsole: false,
    };
  },
});
