<template>
  <div>
    <div class="ml-4 pt-2 header-text-style font-weight-bold">날씨</div>
    <v-divider class="mt-2" />
    <div v-if="loading">
      <div v-if="result != null">
        <div class="ml-4 pt-4 title">서울시</div>
        <v-row no-gutters class="">
          <v-col cols="5">
            <div class="text-center">
              <v-icon :size="setIconSize()">
                {{ changeIcon(result.status) }}
              </v-icon>
            </div>
          </v-col>
          <v-col cols="7">
            <v-row no-gutters>
              <v-col cols="6">
                <div class="display-2 text-right mr-4">
                  {{ result.temp }}
                </div>
              </v-col>
              <v-col cols="6">
                <div class="min-max-text-style">
                  {{ result.min }}/ {{ result.max }}
                </div>
              </v-col>
            </v-row>
            <v-row no-gutters class="mt-n4">
              <v-col cols="12">
                <div
                  class="dust-text-style text-right"
                  :class="setWeatherMargin()"
                >
                  {{ result.status }}
                </div>
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col cols="12" class="text-right">
                <span class="dust-text-style">
                  미세
                </span>
                <span
                  class="dust-text-style"
                  :class="finedustTextColor(result.finedust, 0)"
                >
                  {{ result.finedust }}
                </span>
                <span class="dust-text-style">
                  · 초미세
                </span>
                <span
                  class="dust-text-style mr-2"
                  :class="finedustTextColor(result.ultrafinedust, 1)"
                >
                  {{ result.ultrafinedust }}
                </span>
              </v-col>
            </v-row>
          </v-col></v-row
        >
        <v-row no-gutters :class="setTimeMargin()">
          <v-col cols="12" align="center" justify="center">
            <v-row no-gutters class="">
              <v-col v-for="(data, i) in result.forecast" :key="i">
                <div class="time-weather-text-style">
                  {{ data.value }}
                </div>
              </v-col>
            </v-row>
            <v-row no-gutters :class="setTimeMargin()">
              <v-col v-for="(data, i) in result.forecast" :key="i">
                <div class="time-weather-text-style">
                  {{ data.time }}
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </div>
      <div
        v-else
        class="text-center"
        style="display: flex; 
          align-items: center; 
          justify-content: center;"
      >
        날씨 정보를 불러 올 수 없습니다.
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["eventloading"],
  async mounted() {
    await this.getWeather();
  },
  methods: {
    async getWeather() {
      try {
        var result = await this.$http.get("weather/서울");
        result = result.data;

        if (result.temp != 0) {
          this.result = result;
        }

        this.loading = true;
      } catch {
        this.loading = true;
      }
      if (this.eventloading) {
        this.$session.load = false;
      } else {
        this.$emit("weather-loading", true);
      }
    },
    changeIcon(status) {
      switch (status) {
        case "맑음":
          return "mdi-white-balance-sunny";
        case "흐리고 가끔 비":
          return "mdi-weather-partly-rainy";
        case "구름많음":
          return "mdi-apple-icloud";
        case "흐리고 비":
          return "mdi-weather-rainy";
        case "흐리고 비/눈":
          return "mdi-weather-partly-snowy-rainy";
        case "흐림":
          return "mdi-apple-icloud";
        case "구름많고 한때 비":
          return "mdi-weather-rainy";
        case "비":
          return "mdi-weather-pouring";
        case "눈비":
          return "mdi-weather-snowy-rainy";
        case "눈":
          return "mdi-weather-snowy";
        default:
          return "mdi-white-balance-sunny";
      }
    },
    setIconSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 40;
        case "sm":
          return 60;
        case "md":
          return 80;
        case "lg":
          return 80;
        case "xl":
          return 100;
      }
    },
    setWeatherMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "mr-2";
        case "sm":
          return "mr-2";
        case "md":
          return "mr-4";
        case "lg":
          return "mr-4";
        case "xl":
          return "mr-8";
      }
    },
    setTimeMargin() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "";
        case "sm":
          return "";
        case "md":
          return "mt-2";
        case "lg":
          return "mt-4";
        case "xl":
          return "mt-6";
      }
    },
    finedustTextColor(text, type) {
      var dustClass = "";
      switch (text) {
        case "좋음":
          dustClass += "blue--text ";
          break;
        case "보통":
          dustClass += "green--text ";
          break;
        case "나쁨":
          dustClass += "orange--text ";
          break;
        case "한때나쁨":
          dustClass += "orange--text ";
          break;
        case "매우나쁨":
          dustClass += "red--text ";
          break;
        default:
          dustClass += "black--text ";
          break;
      }
      if (type === 1) {
        switch (this.$vuetify.breakpoint.name) {
          case "xs":
            dustClass += "mr-2";
            break;
          case "sm":
            dustClass += "mr-2";
            break;
          case "md":
            dustClass += "mr-4";
            break;
          case "lg":
            dustClass += "mr-4";
            break;
          case "xl":
            dustClass += "mr-8";
            break;
        }
      }

      return dustClass;
    },
  },
  data() {
    return {
      loading: false,
      result: null,
    };
  },
};
</script>
<style lang="scss">
@media screen and (min-width: 3000px) {
  .header-text-style {
    font-size: 1.4rem;
  }
  .min-max-text-style {
    font-size: 1.8rem;
  }
  .time-weather-text-style {
    font-size: 1.6px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 2rem;
  }
  .time-text-style {
    font-size: 4.8rem;
  }
}
@media screen and (min-width: 1904px) and (max-width: 2999px) {
  .header-text-style {
    font-size: 1.3rem;
  }
  .min-max-text-style {
    font-size: 1.4rem;
  }
  .time-weather-text-style {
    font-size: 1.4rem;
  }
  .time-bottom-margin {
    margin-top: 18px;
  }
  .dust-text-style {
    font-size: 1.4rem;
  }
  .time-text-style {
    font-size: 3.6rem;
  }
}
@media screen and (max-width: 1903px) {
  .header-text-style {
    font-size: 1.2rem;
  }
  .min-max-text-style {
    font-size: 1.2rem;
  }
  .time-weather-text-style {
    font-size: 1.2rem;
  }
  .time-bottom-margin {
    margin-top: 0px;
  }
  .dust-text-style {
    font-size: 1.2rem;
  }
  .time-text-style {
    font-size: 2.4rem;
  }
}
</style>
