import Vue from "vue";
export default new Vue({
  methods: {
    changeLocalDateFormat(date) {
      return new Date(date).toLocaleDateString();
    },
    changeLocalFormat(date) {
      return new Date(date).toLocaleString();
    },
    changeDateFormat(date) {
      var d = new Date(date);

      return (
        d.getFullYear() +
        "-" +
        this.change(d.getMonth() + 1) +
        "-" +
        this.change(d.getDate())
      );
    },
    changeDateNoneFormat(date) {
      return (
        date.getFullYear() +
        "" +
        this.change(date.getMonth() + 1) +
        "" +
        this.change(date.getDate())
      );
    },

    changeDateKorFormat(date) {
      var d = new Date(date);

      return (
        d.getFullYear() +
        "년 " +
        (d.getMonth() + 1) +
        "월 " +
        d.getDate() +
        "일"
      );
    },
    changeTimeFormat(date) {
      var d = new Date(date);
      return (
        this.change(d.getHours()) +
        ":" +
        this.change(d.getMinutes()) +
        ":" +
        this.change(d.getSeconds())
      );
    },
    changeTimeKorFormat(date) {
      var d = new Date(date);

      return (
        this.change(d.getHours() >= 12 ? d.getHours() - 12 : d.getHours()) +
        ":" +
        this.change(d.getMinutes())
      );
    },
    changeFullDateFormat(date) {
      var d = new Date(date);
      return (
        d.getFullYear() +
        "-" +
        this.change(d.getMonth() + 1) +
        "-" +
        this.change(d.getDate()) +
        " " +
        this.change(d.getHours()) +
        ":" +
        this.change(d.getMinutes()) +
        ":" +
        this.change(d.getSeconds())
      );
    },
    changeAmPm(date) {
      var d = new Date(date);
      return d.getHours() < 12 ? "오전" : "오후";
    },
    change(data) {
      return data < 10 ? "0" + String(data) : String(data);
    },
  },
  data() {
    return {
      KR_TIME_DIFF: 9 * 60 * 60 * 1000,
    };
  },
});
