<template>
  <div v-if="restapi">
    <v-toolbar flat class="primary">
      <v-toolbar-title class="text-uppercase font-weight-bold display-1">
        밴드 정보
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-list class="primary">
        <v-list-item>
          <v-list-item-content style="text-align: right">
            <v-list-item-title class="text-h5">
              {{ bandinfo.bid }} &nbsp;{{ bandinfo.alias }}
            </v-list-item-title>

            <v-list-item-subtitle
              class="text-h6"
              :class="banddata.active === 'false' ? 'red--text' : 'green--text'"
              v-text="banddata.active === 'false' ? '연결 종료' : '연결'"
            ></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-toolbar>
    <div class="mx-4 mt-5">
      <div>
        <v-divider></v-divider>
        <div class="mt-2">
          <a
            class="mr-3 font-weight-bold title"
            @click="
              item[0].select = true;
              item[1].select = false;
              item[2].select = false;
            "
            :class="
              item[0].select ? 'font-weight-bold black--text' : 'grey--text'
            "
          >
            요약정보
          </a>
          <a
            class="mr-4 font-weight-bold title"
            @click="
              item[0].select = false;
              item[1].select = true;
              item[2].select = false;
            "
            :class="
              item[1].select ? 'font-weight-bold black--text' : 'grey--text'
            "
            >차트기록</a
          >
          <a
            @click="
              item[0].select = false;
              item[1].select = false;
              item[2].select = true;
            "
            class="font-weight-bold title"
            :class="item[2].select ? ' black--text' : 'grey--text'"
            >이벤트기록</a
          >
        </div>
      </div>
      <div class="mt-2">
        <band-real-time
          v-if="item[0].select"
          :bandinfo="bandinfo"
          :banddata="banddata"
          :alarmList="alarmList"
        />
        <band-history v-else-if="item[1].select" :bandinfo="bandinfo" />
        <band-info v-else :alarmList="alarmList" />

        <!-- <band-info v-if="item[0].select" :alarmList="alarmList" />
     
        <band-history v-else-if="item[1].select" />
        <band-real-time
          v-else
          :bandinfo="bandinfo"
          :banddata="banddata"
          :alarmList="alarmList"
        /> -->
      </div>
    </div>
  </div>
</template>
<script>
import BandInfo from "./BandInfo.vue";
import BandRealTime from "./BandRealTime.vue";
import BandHistory from "./BandHistory.vue";
export default {
  props: ["band_data"],
  watch: {
    band_data() {
      this.onDataHandler(this.band_data);
    },
  },
  components: {
    BandInfo,
    BandRealTime,
    BandHistory,
  },
  created() {
    // this.$session.$on("data", (data) => {
    //   this.onDataHandler(data);
    // });
  },
  async mounted() {
    if (this.$session.user != null) {
      this.bandinfo = JSON.parse(this.$route.query.bandinfo);

      this.alarmList = await this.getAllEventList();
      // this.bandinfo = await this.getBand(JSON.parse(this.$route.query.id));
      if (this.$session.showConsole) console.log(this.bandinfo);
    }
  },

  methods: {
    async getBand(id) {
      try {
        var band = await this.$http.post("bands/detail", { id: id });

        return band.data.data;
      } catch (ex) {
        return null;
      }
    },
    async getAllEventList() {
      try {
        var res = await this.$http.post("events", {
          bid: this.bandinfo.id,
          days: [],
        });
        this.restapi = true;
        var i = 0;

        var result = res.data.data.map((e) => {
          e.id = ++i;
          switch (e.type) {
            case 0: {
              e.type = "낙상감지";
              e.value = "";
              break;
            }
            case 1: {
              e.type = "신호약함";

              break;
            }
            case 2: {
              e.type = "배터리부족";
              break;
            }
            case 3: {
              e.type = "미착용감지";
              e.value = "";
              break;
            }
            case 4: {
              e.type = "연결종료";
              e.value = "";
              break;
            }
          }
          e.datetime = this.$date.changeFullDateFormat(e.datetime);

          return e;
        });

        return result;
      } catch (ex) {
        return [];
      }
    },
    onDataHandler(data) {
      if (data.extAddress.high === this.bandinfo.bid) {
        if (data.active === "false") {
          this.banddata = this.init_band_value;
        } else {
          this.banddata = data;
        }
      }
    },
  },
  data: () => ({
    restapi: false,
    bandinfo: { bid: "", name: "", alias: "", gender: "" },
    init_band_value: {
      bandData: {
        start_byte: "-",
        sample_count: "-",
        fall_detect: "-",
        battery_level: "-",
        hrConfidence: "-",
        spo2Confidence: "-",
        hr: "-",
        spo2: "-",
        motionFlag: "-",
        scdState: "-",
        activity: "-",
        walk_steps: "-",
        run_steps: "-",
        x: "-",
        y: "-",
        z: "-",
        t: "-",
        h: "-",
      },
      rssi: "-",
      active: "false",
    },
    banddata: {
      bandData: {
        start_byte: "-",
        sample_count: "-",
        fall_detect: "-",
        battery_level: "-",
        hrConfidence: "-",
        spo2Confidence: "-",
        hr: "-",
        spo2: "-",
        motionFlag: "-",
        scdState: "-",
        activity: "-",
        walk_steps: "-",
        run_steps: "-",
        x: "-",
        y: "-",
        z: "-",
        t: "-",
        h: "-",
      },
      rssi: "-",
      active: "false",
    },
    mode: true,
    item: [
      { select: true, to: "band/list" },
      { select: false, to: "band/list" },
      { select: false, to: "band/list" },
    ],
    alarmList: [],
  }),
};
</script>
