          </div>
          <v-divider class="mt-2" />
          <VitalSign v-if="loading" :eventList="eventList" />
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="6">
        <v-card height="60vh">
          <!-- <rssi :band_data="band_data" justify="center" align="center" /> -->
          <Map />
        </v-card>
      </v-col>
      <v-col cols="6">
        <v-row
          ><v-col>
            <v-card height="29vh" class="pa-1"
              ><fall-detect height="100vh"/></v-card></v-col
        ></v-row>
        <v-row
          ><v-col>
            <v-card height="29vh">
              <div class="ml-4 pt-2 header-text-style font-weight-bold">
