</template>
<script>
import Time from "./Time.vue";
import FallDetect from "./Falldetect.vue";
import Event from "./Event.vue";
import VitalSign from "./Vitalsign.vue";
import Weather from "./Weather.vue";
// import Rssi from "./Rssi.vue";
import Map from "./Map.vue";
export default {
  props: ["band_data"],

  components: { Time, FallDetect, Event, VitalSign, Weather, Map },
  created() {
    this.$session.load = true;
  },
  async mounted() {
    this.eventList = await this.getEvent();
    if (this.weatherLoading) this.$session.load = false;
  },
  methods: {
    async getEvent() {
      try {
        var date = new Date();

        var res = await this.$http.post("events", {
          bid: -1,
          days: [this.$date.changeDateFormat(date)],
        });

        // var i = 0;
        // var result = res.data.data.map((e) => {
        //   e.id = ++i;
        //   switch (e.type) {
        //     case 0: {
        //       e.type = "낙상감지";
        //       e.value = "";
        //       break;
        //     }
        //     case 1: {
        //       e.type = "신호약함";

        //       break;
        //     }
        //     case 2: {
        //       e.type = "배터리부족";
        //       break;
        //     }
        //     case 3: {
        //       e.type = "미착용감지";
        //       e.value = "";
        //       break;
        //     }
        //     case 4: {
        //       e.type = "연결종료";
        //       e.value = "";
        //       break;
        //     }
        //   }
        //   e.datetime = this.$date.changeTimeFormat(e.datetime);

        //   return e;
        // });
        this.loading = true;
        return res.data.data;
      } catch {
        this.loading = true;
        return [];
      }
    },
    setWeatherLoading(loading) {
      this.weatherLoading = loading;
    },
  },
  data() {
    return {
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: false,
          value: "id",
        },

        { text: "분류", value: "type" },
        { text: "", value: "status" },
      ],
      eventList: [{ id: "0x0000001", type: 1, status: "위험", value: 1 }],
      loading: false,
      weatherLoading: false,
    };
  },
};
</script>
