<template>
  <v-app>
    <drawer v-if="$session.user != null" />
    <v-main class="primary">
      <Alert
        :dialogCheck="alertView"
        @dialog-event="dialogEvent"
        :value="value"
      />
      <div v-if="$session.user != null">
        <v-progress-circular
          class="load"
          v-if="$session.load"
          indeterminate
          color="green"
        ></v-progress-circular>
        <router-view
          :band_data="band_data"
          :gateway_data="gateway_data"
          class="router-view"
        />
      </div>

      <login v-else />
    </v-main>
  </v-app>
</template>

<script>
import drawer from "./components/Drawer.vue";
import login from "./components/Login.vue";
import Alert from "./components/Alert.vue";
export default {
  components: { drawer, login, Alert },
  async mounted() {
    this.$session.$on("data", (data) => {
      if (this.$session.user != null) {
        this.band_data = data;
      }
    });
    this.$session.$on("gateway_connect", (data) => {
      if (this.$session.user != null) {
        if (data.state === 0) {
          this.gatewayAlert(data);
        }
        var index = this.gateway_data
          .map(function(g) {
            return g["panid"];
          })
          .indexOf(data.panid);
        if (index === -1) {
          this.gateway_data.push(data);
        } else {
          this.gateway_data[index] = data;
        }
      }
    });
    //  this.$session.$on("gateway_disconnect", (data) => {
    //   if (this.$session.user != null) {
    //     this.gateway_data = data;
    //   }
    // });
    this.$session.$on("efwbasync", this.onAlertCheck.bind(this));
  },
  data: () => ({
    viewData: {
      value: true,
    },
    alertView: false,
    value: {
      title: "",
      subtitle: "",
      content: "",
      state: 0,
    },
    windowHeight: null,
    band_data: null,
    gateway_data: [],
    bandlist: [],
  }),
  methods: {
    async onAlertCheck(data) {
      var pass = false;
      if (this.$session.user != null) {
        var bands = await this.$http.get(
          "users/bandinfo/" + this.$session.user.id
        );
        bands = bands.data.data;
        for (var i = 0; i < bands.length; i++) {
          if (bands[i].bid == data.bid) pass = true;
        }
        if (pass) this.makeMessage(data);
      }
    },
    gatewayAlert(data) {
      this.$toast.success(
        "[GW] " + data.panid + "가 " + data.ip + "로 연결되었습니다.",
        {
          position: "top-right",
          timeout: 10000,
          closeOnClick: true,
          pauseOnFocusLoss: true,
          pauseOnHover: true,
          draggable: true,
          draggablePercent: 0.6,
          showCloseButtonOnHover: false,
          hideProgressBar: true,
          closeButton: "button",
          icon: true,
          rtl: false,
        }
      );
    },
    makeMessage(data) {
      this.value.subtitle = data.bid;
      switch (data.type) {
        case 0: {
          this.value.title = "낙상 감지";
          this.value.content = 0;
          break;
        }
        case 1: {
          this.value.title = "수신 감도";
          this.value.content = 5000;
          break;
        }
        case 2: {
          this.value.title = " 배터리 용량 부족";
          this.value.content = 5000;
          break;
        }
        case 3: {
          this.value.title = "미착용 감지";
          this.value.content = 5000;
          break;
        }
        case 4: {
          this.value.title = "연결 끊김";
          this.value.content = 0;
          break;
        }
      }
      this.$toast.error(this.value.subtitle + " " + this.value.title, {
        position: "top-right",
        timeout: this.value.content,
        closeOnClick: true,
        pauseOnFocusLoss: true,
        pauseOnHover: true,
        draggable: true,
        draggablePercent: 0.6,
        showCloseButtonOnHover: false,
        hideProgressBar: true,
        closeButton: "button",
        icon: true,
        rtl: false,
      });
    },
    // onAlertCheck(data) {
    //   this.bands[data.shortAddress - 1].bandvalue = data;
    //   if (this.$router.history.current.name != "Login") {
    //     var flag = false;
    //     for (var i = 0; i < this.bands.length; i++) {
    //       if (this.bands[i].bandvalue.bandData.fall_detect === 1) {
    //         flag = true;
    //         this.value.subtitle =
    //           "0x0" +
    //           this.bands[i].bandinfo.bid +
    //           " " +
    //           this.bands[i].bandinfo.name;
    //         this.alertView = true;
    //       }
    //     }
    //     if (!flag) {
    //       this.alertView = false;
    //     }
    //   }
    // },
    dialogEvent(check) {
      this.alertView = check;
    },
  },
};
</script>
<style lang="scss">
.router-view {
  margin: 30px;
}
.switch_mode_image {
  float: right;
}
.load {
  top: 50%;
  left: 50%;
  position: absolute;
  display: block;
  z-index: 99;
  text-align: center;
  transform: translate(-50%, -50%);
}

