<template>
  <div>
    <v-text-field
      v-model="search"
      outlined
      label="Search"
      color="point2"
      append-icon="mdi-magnify"
      class="table-text-style"
      style="width:300px; "
    ></v-text-field>
    <v-data-table
      :headers="header"
      :items="data"
      :items-per-page.sync="itemsPerPage"
      :page.sync="page"
      :search="search"
      :no-data-text="nodata"
      hide-default-footer
      style="background: transparent; "
      class="table-style"
      @click:row="handleClick"
    >
      <template
        v-slot:item.bandvalue.active="{ item }"
        class="list-item table-text-style"
      >
        <v-chip
          :color="item.bandvalue.active === 'false' ? 'grey' : 'green'"
          dark
        >
          {{ item.bandvalue.active === "false" ? "Inactive" : "Active" }}
        </v-chip>
      </template>
      <template v-slot:item.connect_state="{ item }" class="table-text-style">
        <v-chip
          :color="item.connect_state ? 'green' : 'grey'"
          dark
          class="table-text-style"
        >
          {{ item.connect_state ? "Active" : "Inactive" }}
        </v-chip>
      </template>
      <template v-slot:item.connectnum="{ item }" class="table-text-style">
        {{ item.cbandnum }}/{{ item.bandnum }}
      </template>
      <template v-slot:item.connect_time="{ item }" style="font-size:40px">
        {{
          $date.changeFullDateFormat(
            new Date(item.connect_time) - $date.KR_TIME_DIFF
          )
        }}
      </template>
      <template v-slot:item.disconnect_time="{ item }">
        {{
          $date.changeFullDateFormat(
            new Date(item.disconnect_time) - $date.KR_TIME_DIFF
          )
        }}
      </template>
      <template v-slot:item.actions="{ item }">
        <img
          class="list-item"
          :src="setScdState(item.bandvalue)"
          :width="setIconSize()"
        />

        <v-icon :color="setIconColor(item.bandvalue)" :size="setIconSize()">
          {{ setIcon(item.bandvalue, 0) }}
        </v-icon>

        <v-icon :color="setIconColor(item.bandvalue)" :size="setIconSize()">
          {{ setIcon(item.bandvalue, 1) }}
        </v-icon>

        <span
          :class="
            item.bandvalue.active === 'false'
              ? 'align-style-false'
              : 'align-style-true'
          "
        >
          {{ item.bandvalue.bandData.battery_level }}%
        </span>
      </template>
    </v-data-table>
    <template>
      <v-row class="mt-2" align="center" justify="center">
        <v-spacer></v-spacer>

        <span
          class="mr-4
            grey--text"
        >
          Page {{ page }} of {{ numberOfPages }}
        </span>
        <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

        <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
      </v-row>
    </template>
  </div>
</template>
<script>
export default {
  props: ["data", "header", "type"],
  mounted() {},
  beforeDestroy() {},
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    setScdState(bandvalue) {
      if (bandvalue.active === "false")
        return require("../assets/image/falseScdState.svg");
      else if (
        bandvalue.bandData.scdState === 3 ||
        bandvalue.bandData.scdState === 2
      )
        return require("../assets/image/scdState.svg");
      else return require("../assets/image/noScdState.svg");
    },
    setIconColor(bandvalue) {
      if (bandvalue.active === "false") return "grey";
      else return "black";
    },
    setIcon(bandvalue, type) {
      switch (type) {
        case 0:
          if (bandvalue.active === "false")
            return "mdi-signal-cellular-outline";
          else if (bandvalue.rssi > -30) return "mdi-signal-cellular-3";
          else if (bandvalue.rssi > -50) return " mdi-signal-cellular-2";
          else if (bandvalue.rssi > -75) return "mdi-signal-cellular-1";
          else return "mdi-signal-cellular-outline";

        case 1:
          if (bandvalue.active === "false") return "mdi-battery-outline";
          else if (bandvalue.bandData.battery_level > 80)
            return " mdi-battery-high";
          else if (bandvalue.bandData.battery_level > 40)
            return " mdi-battery-medium";
          else if (bandvalue.bandData.battery_level > 15)
            return "mdi-battery-low";
          else return "mdi-battery-outline";
      }
    },
    handleClick(value) {
      switch (this.type) {
        case 0:
          this.$emit("move-detail", value);
          break;
        case 1:
          this.$router.push({
            path: "gateway/detail",
            query: { gatewayinfo: JSON.stringify(value) },
          });
          break;
        case 2:
          this.$router.replace({
            path: "/user/detail",
            query: {
              user: JSON.stringify(value),
              group: JSON.stringify({ groupname: "dtriple" }),
            },
          });
          break;
      }
    },
    setIconSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 28;
        case "sm":
          return 30;
        case "md":
          return 30;
        case "lg":
          return 32;
        case "xl":
          return 34;
      }
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.data.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
  data: () => ({
    search: "",
    nodata: "",
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
  }),
};
</script>
<style lang="scss">
.list-item {
  vertical-align: middle;
}

.align-style-true {
  width: 0px;
  font-weight: bold;
  color: black;
  display: inline-block;
}
.align-style-false {
  width: 0px;
  font-weight: bold;
  color: grey;
  display: inline-block;
}
.table-style {
  background: transparent;
  border: 1px solid rgb(190, 189, 189);
}

@media screen and (min-width: 3000px) {
  .table-text-style {
    font-size: 1.4rem;
  }
  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.4rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.4rem;
  }
}
@media screen and (min-width: 1904px) {
  .table-text-style {
    font-size: 1.2rem;
  }

  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1.2rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1903px) {
  .table-text-style {
    font-size: 20px;
  }

  .v-data-table
    > .v-data-table__wrapper
    > table
    > .v-data-table-header
    > tr
    > th {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td {
    font-size: 1rem;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td > span {
    font-size: 1rem;
  }
}
</style>
