<template>
  <v-navigation-drawer permanent app clipped class="secondary">
    <template v-slot:prepend>
      <v-list-item link to="/">
        <v-list-item-content>
          <v-list-item-title
            class="display-1 text-center white--text font-weight-bold"
            >WELL-O</v-list-item-title
          >
        </v-list-item-content>
      </v-list-item>
    </template>
    <v-list>
      <v-list-item
        v-for="(item, index) in menu_list"
        router-link
        :to="item.to"
        @click="clicked = index"
        :key="index"
        :class="
          $route.fullPath === item.to
            ? 'point2'
            : item.to !== '/' && $route.fullPath.indexOf(item.to) !== -1
            ? 'point2'
            : 'secondary'
        "
      >
        <v-list-item-content>
          <v-list-item-title class="white--text ">{{
            item.name
          }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <template v-slot:append>
      <div class="pa-2">
        <v-btn depressed block class="point2 white--text" @click="logout()">
          Logout
        </v-btn>
      </div>
    </template>
  </v-navigation-drawer>
</template>

<script>
export default {
  methods: {
    logout() {
      this.$session.releaseSession();
    },
  },
  data: () => ({
    menu_list: [
      { name: "대시보드", to: "/" },
      { name: "밴드", to: "/band" },
      { name: "게이트웨이", to: "/gateway" },
      { name: "사용자", to: "/user" },
      { name: "로그", to: "/log" },
    ],
    clicked: 0,
  }),
  watch: {
    $route() {},
  },
};
</script>
