import Vue from "vue";
import VueRouter from "vue-router";
import DoshBoard from "../components/dashboard/DashBoard.vue";
import Band from "../components/band/Band.vue";
import BandDetail from "../components/band/BandDetail.vue";
import Gateway from "../components/gateway/Gateway.vue";
import GatewayDetail from "../components/gateway/GatewayDetail.vue";
import User from "../components/user/User.vue";
import UserDetail from "../components/user/UserDetail.vue";
import Log from "../components/Log.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "DashBoard",
    component: DoshBoard,
  },
  {
    path: "/band",
    name: "Band",
    component: Band,
  },
  {
    path: "/band/detail",
    name: "BandDetail",
    component: BandDetail,
  },

  {
    path: "/gateway",
    name: "Gateway",
    component: Gateway,
  },
  {
    path: "/gateway/detail",
    name: "GatewayDetail",
    component: GatewayDetail,
  },
  {
    path: "/user",
    name: "User",
    component: User,
  },
  {
    path: "/user/detail",
    name: "UserDetail",
    component: UserDetail,
  },
  {
    path: "/log",
    name: "Log",
    component: Log,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
