<template>
  <div>
    <Table
      :data="bandinfo"
      :header="headers"
      :type="0"
      @move-detail="moveBandDetail"
    />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  props: ["bandinfo"],
  mounted() {},
  methods: {
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        if (this.$session.showConsole) console.log(group[0].permission);
        switch (group[0].permission) {
          case 0:
            band = await this.rest.bands_list();
            if (band != null) {
              bands = band.data.data;
            }

            break;
          case 1:
            // console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            // console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            // console.log(bands[i].id);
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
            if (this.$session.showConsole) console.log(bands[i]);
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    moveBandDetail(value) {
      this.$emit("move-detail", value);
    },
  },

  data: () => ({
    restapi: false,
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: -80,
      extAddress: {
        row: 0,
      },
      active: "true",
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
    headers: [
      {
        text: "Status",
        align: "start",
        sortable: true,
        value: "bandvalue.active",
      },
      {
        text: "ID",
        value: "num",
      },
      { text: "Band ID", value: "bid" },
      { text: "Alias", value: "alias" },
      { text: "Name", value: "name" },
      { text: "State", value: "actions", sortable: false },
    ],
  }),
};
</script>
<style lang="scss"></style>
