<template>
  <div>
    <v-toolbar flat class="primary">
      <v-spacer></v-spacer>
      <v-img
        class="switch_mode_image"
        :src="
          mode
            ? require('../../assets/image/switch_list_btn.svg')
            : require('../../assets/image/switch_node_btn.svg')
        "
        max-height="60"
        max-width="120"
        @click="mode = !mode"
      ></v-img>
    </v-toolbar>
    <band-list
      :bandinfo="bandinfo"
      @move-detail="moveBandDetail"
      v-if="mode && restapi"
    />
    <band-node
      v-else-if="!mode && restapi"
      :bandinfo="bandinfo"
      @move-detail="moveBandDetail"
    />
  </div>
</template>
<script>
import BandList from "./BandList.vue";
import BandNode from "./BandNode.vue";
export default {
  props: ["band_data"],
  watch: {
    band_data() {
      this.onDataHandler(this.band_data);
    },
  },
  components: {
    BandList,
    BandNode,
  },

  async mounted() {
    this.$session.load = false;
    if (this.$session.user != null) {
      await this.getBandList();
    }
  },
  methods: {
    async getBandList() {
      try {
        var band = null;
        var bands = null;

        switch (this.$session.user.permission) {
          case 0:
            band = await this.$http.get("bands/list");
            this.restapi = true;
            if (band != null) bands = band.data.data;

            break;
          case 1:
            if (this.$session.showConsole) console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            this.restapi = true;
            bands = band.data.data;
            break;
          case 2:
            console.log(this.$session.user.id);
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            this.restapi = true;
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            if (this.$session.showConsole) console.log(bands[i].id);
            this.bandinfo.push({
              num: i + 1,
              id: bands[i].id,
              bid: bands[i].bid,
              alias: bands[i].alias,
              name: bands[i].name,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              connect_state: bands[i].connect_state,
              disconnect_time: bands[i].disconnect_time,
              connect_time: bands[i].connect_time,

              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },

    onDataHandler(data) {
      if (this.$session.showConsole) console.log(data);
      var index = this.bandinfo
        .map(function(d) {
          return d["bid"];
        })
        .indexOf(data.extAddress.high);
      if (index !== -1) {
        switch (data.active) {
          case "false":
            this.bandinfo[index].bandvalue = this.bandvalue;
            break;
          case "true":
            this.bandinfo[index].bandvalue = data;
            break;
        }
      }
    },

    async listClick(bandinfo) {
      if (await this.$session.historyCheck()) {
        this.$router.push({
          name: "Band Info",
          query: { bandInfo: JSON.stringify(bandinfo) },
        });
      } else {
        this.$session.releaseSession();
        this.$router.push("/");
      }
    },
    moveBandDetail(band) {
      this.$router.replace({
        path: "band/detail",
        query: {
          bandinfo: JSON.stringify({
            num: band.num,
            id: band.id,
            bid: band.bid,
            alias: band.alias,
            name: band.name,
            birth: band.birth,
            gender: band.gender,
            created: band.created,
            connect_state: band.connect_state,
            disconnect_time: band.disconnect_time,
            connect_time: band.connect_time,
          }),
        },
      });
    },
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
  },
  data: () => ({
    mode: false,
    bandinfo: [],
    restapi: false,
    data: null,
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: "",
      extAddress: {
        row: 0,
      },
      active: "false",
    },
  }),
};
</script>
