<template align="center">
  <div v-if="loading">
    <span class="date-text-style">
      {{ $date.changeDateKorFormat(nowDate) }}
    </span>
    <v-row align="center">
      <v-col>
        <span class="date-text-style">{{ $date.changeAmPm(nowDate) }} </span>
        <span class="time-text-style font-weight-bold">
          {{ $date.changeTimeKorFormat(nowDate) }}
        </span>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  created() {
    setInterval(() => this.getNowDate(), 1000);
  },
  methods: {
    getNowDate() {
      this.loading = true;
      this.nowDate = new Date();
    },
  },
  data() {
    return {
      nowDate: null,
      loading: false,
    };
  },
};
</script>
<style lang="scss">
@media screen and (min-width: 3000px) {
  .date-text-style {
    font-size: 3.6rem;
  }
  .time-text-style {
    font-size: 4.8rem;
  }
}
@media screen and (min-width: 1904px) {
  .date-text-style {
    font-size: 2.4rem;
  }
  .time-text-style {
    font-size: 3.6rem;
  }
}
@media screen and (max-width: 1903px) {
  .date-text-style {
    font-size: 1.8rem;
  }
  .time-text-style {
    font-size: 2.4rem;
  }
}
</style>
