<template>
  <v-data-table
    class="pa-1"
    falt
    height="26vh"
    :headers="headers"
    :items="getEventList"
    hide-default-footer
    :items-per-page="-1"
  ></v-data-table>
</template>

<script>
export default {
  props: ["eventList"],
  computed: {
    getEventList() {
      var el = [];

      for (var i in this.eventList) {
        var type = null;
        var value = "";
        switch (this.eventList[i].type) {
          case 0: {
            type = "낙상발생";
            value = "위험";
            break;
          }
          case 5: {
            type = "심박수이상";
            value = this.eventList[i].value >= 150 ? "위험" : "주의";
            break;
          }
        }
        if (type != null) {
          el.push({
            bid: this.eventList[i].bandid,
            type: type,
            value: value,
          });
        }
      }
      return el;
    },
  },
  data() {
    return {
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: false,
          value: "bid",
        },

        { text: "분류", value: "type" },
        { text: "", value: "value" },
      ],
    };
  },
};
</script>
<style lang="scss">
@media screen and (min-width: 3000px) {
  .header-text-style {
    font-size: 1.4rem;
  }
  .min-max-text-style {
    font-size: 1.4rem;
  }
  .time-top-margin {
    margin-top: 24px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 1.4rem;
  }
  .time-text-style {
    font-size: 4.8rem;
  }
}
@media screen and (min-width: 1904px) {
  .header-text-style {
    font-size: 1.3rem;
  }
  .min-max-text-style {
    font-size: 1.2rem;
  }
  .time-top-margin {
    margin-top: 24px;
  }
  .time-bottom-margin {
    margin-top: 24px;
  }
  .dust-text-style {
    font-size: 1.2rem;
  }
  .time-text-style {
    font-size: 3.6rem;
  }
}
@media screen and (max-width: 1903px) {
  .header-text-style {
    font-size: 1.2rem;
  }
  .min-max-text-style {
    font-size: 1rem;
  }
  .time-top-margin {
