<template>
  <div>
    <Table
      :data="gatewayinfo"
      :header="headers"
      :type="1"
      @move-detail="moveGWDetail"
    />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  props: ["gatewayinfo"],
  watch: {},
  mounted() {},
  data(vm) {
    return {
      search: "",
      nodata: "",
      KR_TIME_DIFF: 9 * 60 * 60 * 1000,
      itemsPerPageArray: [5, 10, 15, 20],
      page: 1,
      itemsPerPage: 10,
      headers: vm.computeHeader(),
    };
  },
  methods: {
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        switch (group[0].permission) {
          case 0:
            band = await this.$http.get("bands/list");
            bands = band.data.data;
            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    onDataHandler(data) {
      if (this.$session.showConsole) console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    async listClick(bandinfo) {
      //console.log(bandinfo);
      if (await this.$session.historyCheck()) {
        this.$router.replace({
          name: "Band Info",
          query: { bandInfo: JSON.stringify(bandinfo) },
        });
      } else {
        this.$session.releaseSession();
        this.$router.replace("/");
      }
    },
    moveGWDetail(gw) {
      this.$router.push({
        path: "gateway/detail",
        query: { gatewayinfo: JSON.stringify(gw) },
      });
    },
    computeHeader() {
      var result = [
        {
          text: "Status",
          align: "start",
          sortable: true,
          value: "connect_state",
        },
        {
          text: "ID",
          value: "num",
        },
        { text: "Pan ID", value: "pid" },
        { text: "Alias", value: "alias" },
        { text: "Band Num", value: "connectnum" },
        {
          text: "Connect Time",
          value: "connect_time",
          filter: (value) => {
            return this.dateCompute(value);
          },
          sortable: false,
        },
        { text: "Disconnect Time", value: "disconnect_time", sortable: false },
      ];
      console.log(result);
      return result;
    },
    dateCompute(value) {
      console.log(
        this.$date.changeFullDateFormat(
          new Date(value) - this.$date.KR_TIME_DIFF
        )
      );
      return this.$date.changeFullDateFormat(
        new Date(value) - this.$date.KR_TIME_DIFF
      );
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.gatewayinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.list-item {
  align-self: center; // [jk]
}
.text-style {
  font-size: 1.2rem;
}
.align-style {
  width: 40px;
}
</style>
