<template>
  <div>
    <Table :data="userlist" :header="headers" :type="2" />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  mounted() {
    this.getUserList();
  },
  data: () => ({
    bandinfo: [
      {
        bandvalue: {
          bandData: {
            start_byte: "",
            sample_count: "",
            fall_detect: "",
            battery_level: "",
            hrConfidence: "",
            spo2Confidence: "",
            hr: "",
            spo2: "",
            motionFlag: "",
            scdState: "",
            activity: "",
            walk_steps: "",
            run_steps: "",
            x: "",
            y: "",
            z: "",
            t: "",
            h: "",
          },
          rssi: -80,
          extAddress: {
            row: 0,
          },
          active: "true",
        },
      },
      {
        bandvalue: {
          bandData: {
            start_byte: "",
            sample_count: "",
            fall_detect: "",
            battery_level: "",
            hrConfidence: "",
            spo2Confidence: "",
            hr: "",
            spo2: "",
            motionFlag: "",
            scdState: "",
            activity: "",
            walk_steps: "",
            run_steps: "",
            x: "",
            y: "",
            z: "",
            t: "",
            h: "",
          },
          rssi: -80,
          extAddress: {
            row: 0,
          },
          active: "false",
        },
      },
    ],
    userlist: [],
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: -80,
      extAddress: {
        row: 0,
      },
      active: "true",
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
    detail: false,
    group: { groupname: "" },
    headers: [
      {
        text: "ID",
        align: "start",
        sortable: true,
        value: "num",
      },
      {
        text: "User ID",
        value: "uid",
      },
      { text: "Name", value: "name" },
      { text: "Permission", value: "permission" },
      { text: "Group", value: "groupname" },
    ],
  }),
  methods: {
    async getUserList() {
      try {
        var res = await this.$http.get(
          "users/samegroup/" + this.$session.user.id
        );
        var groupinfo = await this.$http.post("groups/detail", {
          gid: res.data.data.gid,
        });
        this.group = groupinfo.data.groups;
        this.userlist = res.data.data.userlist;
        for (var i = 0; i < this.userlist.length; i++) {
          this.userlist[i].groupname = this.group.groupname;
          this.userlist[i].num = i + 1;
          switch (this.userlist[i].permission) {
            case 0:
              this.userlist[i].permission = "admin";
              break;
            case 1:
              this.userlist[i].permission = "manager";
              break;
          }
        }
        console.log(this.userlist);
      } catch (ex) {
        this.userlist = [];
      }
    },
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        switch (group[0].permission) {
          case 0:
            band = await this.$http.get("bands/list");
            bands = band.data.data;
            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
        }
        if (bands != null) {
          for (var i = 0; i < bands.length; i++) {
            this.bandinfo.push({
              alias: bands[i].alias,
              bid: bands[i].bid,
              birth: bands[i].birth,
              created: bands[i].created,
              gender: bands[i].gender,
              id: bands[i].id,
              name: bands[i].name,
              bandvalue: this.bandvalue,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    onDataHandler(data) {
      //console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    listClick(user) {
      this.$router.replace({
        path: "/user/detail",
        query: {
          user: JSON.stringify(user),
          group: JSON.stringify(this.group),
        },
      });
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(5 / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
